import { Form, Icon, Input, Modal, Select } from "antd"
import React from "react"

interface IProps {
  name: string
  personalCode: string
  phoneNumber: string
  userRank: string
  visible: boolean
  loading: boolean
  onInputChange: (
    event: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => void
  onSubmit: (e: React.MouseEvent<HTMLElement, MouseEvent>) => void
  onCancel: (e: React.MouseEvent<HTMLElement, MouseEvent>) => void
  onSelectChange: (value: any) => void
}

const CreateUserModalPresenter: React.SFC<IProps> = ({
  name,
  personalCode,
  phoneNumber,
  userRank,
  visible,
  loading,
  onInputChange,
  onSubmit,
  onCancel,
  onSelectChange
}) => (
  <Modal
    title=" "
    visible={visible}
    okText=""
    cancelText=""
    onOk={onSubmit}
    confirmLoading={loading}
    onCancel={onCancel}
  >
    <Form>
      <Form.Item>
        <Input
          required={true}
          name="name"
          value={name}
          onChange={onInputChange}
          prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25" }} />}
          placeholder=""
        />
      </Form.Item>
      <Form.Item>
        <Input
          required={true}
          name="personalCode"
          value={personalCode}
          onChange={onInputChange}
          prefix={<Icon type="tag" style={{ color: "rgba(0,0,0,.25" }} />}
          placeholder=""
        />
      </Form.Item>
      <Form.Item>
        <Input
          required={true}
          name="phoneNumber"
          value={phoneNumber}
          onChange={onInputChange}
          prefix={<Icon type="phone" style={{ color: "rgba(0,0,0,.25" }} />}
          placeholder=""
        />
      </Form.Item>
      <Form.Item label="">
        <Select defaultValue={userRank} onChange={onSelectChange}>
          <Select.Option value={"ONE"}>1</Select.Option>
          <Select.Option value={"TWO"}>2</Select.Option>
          <Select.Option value={"THREE"}>3</Select.Option>
        </Select>
      </Form.Item>
    </Form>
  </Modal>
)

export default CreateUserModalPresenter
